/*
* Video Label
* 
* @version 0.0.1
* @param {string} text - 
* @param {bool} isLive -  Live Style
* @param {bool} isComingSoon -  Coming Soon Style
* @param {bool} isNewSeason -  New Season Style
* @param {bool} isDesktop -  Desktop Style
* @param {bool} isHotPicks -  HotPicks Style
* @param {bool} hasBoxShadow -  Box Shadow
* @todo BigScreen Style
* @todo  HotPick Style  HotPicks Component
*/

import React from 'react';
import classNames from 'classnames';

class VideoLabel extends React.Component {
  render() {
    const { text, isLive, isNewSeason, isComingSoon, isDesktop, isHotPicks, hasBoxShadow } = this.props;

    return text ? (
      <div
        className={classNames(
          'video-label',
          { '_live': isLive },
          { '_coming-soon': isComingSoon },
          { '_new-season': isNewSeason },
          { '_hot-picks': isHotPicks },
          { '_box-shadow': hasBoxShadow || isLive },
          { '_desktop': isDesktop }
        )}
      >
        {text}
      </div>
    ) : <span></span>;
  }
}

VideoLabel.defaultProps = {
  text: '',
  isLive: false,
  isComingSoon: false,
  isNewSeason: false,
  isDesktop: false,
  isHotPicks: false,
  hasBoxShadow: false
};

export default VideoLabel;
